.btn {
  position: relative;
  button {
    background-color: transparent;
    border: 0;
    cursor: pointer;
    font-size: 14px;
  }

  .btnPrevious {
    width: 100px;
    height: 46px;
  }

  .btnNext {
    position: absolute;
    right: 0px;
    width: 174px;
    height: 46px;
    background-color: #f67599;
    color: white;
    border-radius: 7px;
  }
}

